const AWS = require('aws-sdk')
const getParams = require('./lib/getParams')
const sns = new AWS.SNS({apiVersion: '2010-03-31'});

async function recordProcessor(record) {
  console.log('Stream record: ', record);

  console.log("\n\nLoading Event Record\n\n", record.eventName);
  try {
    const {
      eventName,
      dynamodb,
    } = record

    const {
      NewImage = null,
      OldImage = null,
      SequenceNumber,
      ApproximateCreationDateTime
    } = dynamodb

    console.log({ NewImage, OldImage })

    const dbStreamImage = NewImage ? NewImage : OldImage
    console.log('dbStreamImage', dbStreamImage)

    const message = {
      MessageAttributes: {
        EventType: {
          DataType: 'String', /* required */
          StringValue: `${eventName.toUpperCase()}`
        },
        JobType: {
          DataType: 'String', /* required */
          StringValue: `${dbStreamImage.Type.S.toLowerCase()}`
        },
        Visibility: {
          DataType: 'String', /* required */
          StringValue: `${dbStreamImage.Visibility.S.toLowerCase()}`
        }
      },
      MessageStructure: 'json',
      Message: JSON.stringify({
        default:`New job "${eventName.toUpperCase()}" posted by ${dbStreamImage.OwnerId.S} for place ${dbStreamImage.PlaceId.S}`,
        email:`New job "${eventName.toUpperCase()}" posted by ${dbStreamImage.OwnerId.S} for place ${dbStreamImage.PlaceId.S}`,
        sqs: JSON.stringify(dynamodb)
      }),
      Subject: "New DynamoDB Stream Record",
      // Message: `New job "${eventName.toUpperCase()}" posted by ${dbStreamImage.OwnerId.S} for place ${dbStreamImage.PlaceId.S}`,
      TopicArn: process.env.SNS_TOPIC_ARN
    }
  console.log('message', message)
    const publishedMessage = await sns.publish(message).promise()
    
    console.log('messageId', publishedMessage.MessageId)

    return Promise.resolve(`Successfully processed stream record ${SequenceNumber} (sequence number) at ${ApproximateCreationDateTime}. Message ${publishedMessage.MessageId} successfully sent`)

  } catch (error) {
    // throw error
    return Promise.reject(error)
  }

}
exports.handler = async (event, context, callback) => {
  try {
    for (record of event.Records) {
      const processedRecords = await recordProcessor(record)
      console.log('processedRecords', processedRecords)
    }
    return `Successfully processed ${event.Records.length} records.`
  } catch (error) {
    console.log({
      message: 'A problem ocurred',
      event: JSON.stringify(event)
    })
    throw error
    
  }
}























